/* NAVBAR */
.navbar .nav .nav-item .nav-link {
    font: 500 14px "Roboto", sans-serif;
    text-decoration: none;
    color: #05364d;
    padding: 20px;
    margin: 0px 10px;
    font-size: large;
}
.navbar .nav{
        margin-left: 5rem;
}
.navbar .nav .nav-item:hover .nav-link, .navbar .nav .nav-item.active .nav-link {
    color: #efb6df;
}

/* HEADER */

.bg-01{
    background-image: url(/static/media/jumbotron-bg_photo.c5d6b03f.png);
    background-repeat: no-repeat;
    height: 800px;
    background-size: 800px;
    background-position-x: right;
}

@media (max-width: 800px) {
    .bg-01 {
        background-image: url(/static/media/TRY-1.fea38022.png);
    }
    .home_banner_area{
            margin-top: 0px;
    }
    .navbar .navbartoggler{
            background-color: gray;

    }
    .primary_btn {
        margin-bottom: 10px;
    }

}

.home_banner_area{
        margin-top: 150px;
}
.home_banner_area .banner_content h3 {
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 20px;
    letter-spacing: 2px;
    color: #797979;
}
.home_banner_area .banner_content h1 {
    color: rgba(5, 54, 77, 0.9);
    margin-top: 20px;
    font-size: 50px;
    font-weight: 500;
    line-height: 60px;
    margin-bottom: 20px;
}
.home_banner_area .banner_content h5{
    color: rgba(5, 54, 77, 0.8);
}

.home_banner_area .banner_content .social_icons a {
    color: rgba(5, 54, 77, 0.5);
    font-size: 26px;
    display: inline-block;
    margin-right: 16px;
    cursor: pointer;
    transition: all 0.4s ease 0s;
}

.gradient-color, .home_banner_area .banner_content .social_icons a:hover, .statistics_item h3 {
    background: linear-gradient(90deg,  #1345e6 0%,  #ed239f 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.home_banner_area .banner_content .primary_btn {
    margin-right: 20px;
}
.banner_area {
    position: relative;
    z-index: 1;
}
.primary_btn {
    display: inline-block;
    background: linear-gradient(to right,  #1345e6 0%,  #ed239f 51%,  #1345e6 100%);
    color: #fff;
    letter-spacing: 0px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 50px;
    padding: 0px 38px;
    outline: none !important;
    text-align: center;
    cursor: pointer;
    text-transform: uppercase;
    border-radius: 5px;
    background-size: 200% auto;
    text-decoration: none;
    
}
.primary_btn:hover {
        text-decoration: none;
    background-position: right center;
    color: #fff;
    transition: all 0.4s ease 0s;
}


.roll-in-left {
	-webkit-animation: roll-in-left 3s cubic-bezier(0.190, 1.000, 0.220, 1.000) both;
	        animation: roll-in-left 3s cubic-bezier(0.190, 1.000, 0.220, 1.000) both;
}

.bounce-in-top {
        color: #fff;
	-webkit-animation: bounce-in-top 2.0s cubic-bezier(1.000, 0.000, 0.000, 1.000) ;
	        animation: bounce-in-top 2.0s cubic-bezier(1.000, 0.000, 0.000, 1.000) ;
}

