.footer_area .footer_social {
    text-align: center;
    margin: 5px 0px 30px;
    padding-top: 30px;
}
.footer_area .footer_social a {
    display: inline-block;
    width: 45px;
    height: 45px;
    line-height: 45px;
    text-align: center;
    background: #ffffff;
    border-radius: 36px;
    color: #05364d;
    font-size: 16px;
    margin-right: 12px;
    transition: all 0.4s ease 0s;
}
.footer_area .footer_social a:last-child {
    margin-right: 0px;
}
.footer_area .footer_social a:hover {
    color: #ffffff;
}

.footer_area .footer_social a:hover {
    background: linear-gradient(90deg,  #1345e6 0%,  #ed239f 100%);
}
/* About Page */
.pp{
    position: -webkit-sticky;
    position: sticky;
    top: 0rem;
    z-index: -1;
}
.about_area{
    margin: 20px;
}

.about_area .about_img img {
    margin-left: 20px;
    margin-top: 150px;
    max-width: 85%;
    height: auto;
}

.main_title p.top_text {
    color: rgba(5, 54, 77, 1);
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 500;
    position: relative;
    margin-bottom: 10px;
}
.main_title p.top_text span {
    position: relative;
    top: -2px;
    display: inline-block;
    margin-left: 5px;
    width: 80px;
    height: 4px;
    background: rgba(237, 35, 159, 0.6);
    filter: blur(4px);
    -o-filter: blur(4px);
    -ms-filter: blur(4px);
    -moz-filter: blur(4px);
    -webkit-filter: blur(4px);
    border-radius: 5px;
}
.main_title h2 {
    color: rgba(5, 54, 77, 1);
    padding: 20px 0px;
    font-size: 35px;
    font-weight: 700;
    margin-bottom: 0px;
    line-height: 52px;
    text-transform: capitalize;
}

@media (max-width: 800px) {
    .about_area .about_img img{
        margin-top: -40px;
        margin-left: auto;
    }
    .pp{
        position: relative;
        z-index: 0;
    }

}
