/* d80700b2 */
.vertical-timeline-element-subtitle .languages{
    background-color: #7a0051c7;
    border-radius: 10px;
    padding: 5px 10px;
    margin-right: 20px;;
    font-size: 14px;
    color: white;

}
.vertical-timeline-element-title{
    margin-top: 10px;
}

.vertical-timeline-element-subtitle .pro .btn{
  display: inline-block;
  border-radius: 4px;
  background-color: whitesmoke;
  border: none;
  text-align: center;
  font-size: 14px;
  padding: 10px 10px;
  width: 100px;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;


}
.vertical-timeline-element-subtitle .pro .btn span{
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}
.vertical-timeline-element-subtitle .pro .btn span::after{
    content: '\00bb';
    color: black;
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

.vertical-timeline-element-subtitle .pro .btn:hover span{
    padding-right: 10px;
}
.vertical-timeline-element-subtitle .pro .btn:hover span:after {
  opacity: 1;
  right: 0;
}
